@import url(https://fonts.googleapis.com/css?family=Lora&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.App__new, .App__tweet {
  font-family: 'Roboto', sans-serif;
  opacity: 1;
  cursor: pointer;
  -webkit-transition: opacity 250ms;
  transition: opacity 250ms;
  outline: none;
  color: #357edd;
  text-decoration: unset; }
  .App__new:hover, .App__tweet:hover {
    opacity: .8; }

.App__new, .App__tweet {
  border: none;
  border-radius: 0.25rem;
  color: #f6fffe;
  background-color: #357edd;
  padding: .8rem 1.2rem; }

.App {
  font-family: 'Roboto', sans-serif;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #357edd; }
  .App__box {
    width: 500px;
    max-width: 92%;
    background-color: #f6fffe;
    padding: 2rem 3rem;
    color: #357edd;
    box-shadow: 0.5rem 0.5rem 1rem 0 rgba(0, 0, 0, 0.2);
    border-radius: 0.25rem; }
  .App__body {
    margin-bottom: 2rem; }
  .App__footer {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  .App__text {
    font-family: 'Lora', serif;
    text-align: center;
    font-weight: 500;
    font-size: 2em; }
  .App__author {
    text-align: right;
    font-size: 1em; }
  .App__icon {
    width: 1.2rem;
    height: auto;
    fill: #f6fffe; }

